"use client"

import type React from "react"
import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { ArrowLeft, Trash2 } from "lucide-react"
import Link from "next/link"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

interface Video {
  id: string
  title: string
  description: string
  price: number
  thumbnail_url: string
  created_at: string
}

interface Purchase {
  id: string
  user_id: string
  video_id: string
  amount: number
  payment_method: string
  status: string
  created_at: string
  user_email?: string
  video_title?: string
}

export default function AdminPage() {
  const [videos, setVideos] = useState<Video[]>([])
  const [purchases, setPurchases] = useState<Purchase[]>([])
  const [title, setTitle] = useState("")
  /* eslint-disable @typescript-eslint/no-explicit-any */
  const [pricingTiers, setPricingTiers] = useState<Record<string, string>>({})
  const [description, setDescription] = useState("")
  // const [price, setPrice] = useState("") // Deprecated single price
  const [thumbnailUrl, setThumbnailUrl] = useState("")
  const [productFileUrl, setProductFileUrl] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [isUploading, setIsUploading] = useState(false)
  const [isFileUploading, setIsFileUploading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const router = useRouter()
  const supabase = createClient()

  const handlePriceChange = (duration: string, value: string) => {
    setPricingTiers(prev => ({ ...prev, [duration]: value }))
  }

  const handleProductFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) return

    setIsFileUploading(true)
    setError(null)

    try {
      const fileExt = file.name.split(".").pop()
      const fileName = `${Date.now()}.${fileExt}`
      const filePath = `files/${fileName}`

      const { error: uploadError } = await supabase.storage
        .from("product-files")
        .upload(filePath, file)

      if (uploadError) throw uploadError

      const { data: { publicUrl } } = supabase.storage
        .from("product-files")
        .getPublicUrl(filePath)

      setProductFileUrl(publicUrl)
    } catch (error) {
      console.error(error)
      setError("파일 업로드에 실패했습니다. (product-files 버킷이 존재하는지 확인해주세요)")
    } finally {
      setIsFileUploading(false)
    }
  }

  const handleImageUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) return

    setIsUploading(true)
    setError(null)

    try {
      const fileExt = file.name.split(".").pop()
      const fileName = `${Date.now()}.${fileExt}`
      const filePath = `thumbnails/${fileName}`

      const { error: uploadError } = await supabase.storage
        .from("product-images")
        .upload(filePath, file)

      if (uploadError) throw uploadError

      const { data: { publicUrl } } = supabase.storage
        .from("product-images")
        .getPublicUrl(filePath)

      setThumbnailUrl(publicUrl)
    } catch (error) {
      console.error(error)
      setError("이미지 업로드에 실패했습니다. (버킷이 존재하는지 확인해주세요)")
    } finally {
      setIsUploading(false)
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const {
          data: { user },
        } = await supabase.auth.getUser()
        if (!user) {
          router.push("/auth/login")
          return
        }

        const { data: profile } = await supabase.from("profiles").select("is_admin").eq("id", user.id).single()

        if (!profile?.is_admin) {
          router.push("/dashboard")
          return
        }

        const { data: videosData } = await supabase
          .from("videos")
          .select("*")
          .eq("user_id", user.id)
          .order("created_at", { ascending: false })

        const { data: purchasesData } = await supabase
          .from("purchases")
          .select(
            `
            id,
            user_id,
            video_id,
            amount,
            payment_method,
            status,
            created_at,
            profiles(email),
            videos(title)
          `,
          )
          .order("created_at", { ascending: false })

        setVideos(videosData || [])
        setPurchases(
          purchasesData?.map((p: any) => ({
            id: p.id,
            user_id: p.user_id,
            video_id: p.video_id,
            amount: p.amount,
            payment_method: p.payment_method,
            status: p.status,
            created_at: p.created_at,
            user_email: p.profiles?.email || "알 수 없음",
            video_title: p.videos?.title || "삭제됨",
          })) || [],
        )
      } catch (error) {
        console.error("데이터 로드 실패:", error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchData()
  }, [router, supabase])

  const handleAddVideo = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)
    setIsSubmitting(true)

    // Base price is 1-day price or 0
    const basePrice = Number.parseFloat(pricingTiers["1일"] || "0")

    if (!title || !description || !thumbnailUrl || basePrice === 0) {
      setError("모든 필드와 1일 가격은 필수입니다")
      setIsSubmitting(false)
      return
    }

    try {
      const {
        data: { user },
      } = await supabase.auth.getUser()
      if (!user) throw new Error("사용자를 찾을 수 없습니다")

      // Hack: Store pricing config in description as metadata since we can't easily alter schema
      const pricingMetadata = JSON.stringify(pricingTiers)

      // Hack: Store product file URL in description as metadata
      const fileMetadata = productFileUrl ? `<!--FILE_URL:${productFileUrl}-->` : ""

      const fullDescription = `${description}\n\n<!--PRICING:${pricingMetadata}-->${fileMetadata}`

      const { data, error: insertError } = await supabase
        .from("videos")
        .insert({
          title,
          description: fullDescription,
          price: basePrice, // Store 1-day price as base
          thumbnail_url: thumbnailUrl,
          user_id: user.id,
          is_admin: true,
        })
        .select()

      if (insertError) throw insertError

      setVideos([data[0], ...videos])
      setTitle("")
      setDescription("")
      setPricingTiers({})
      setThumbnailUrl("")
    } catch (error) {
      setError(error instanceof Error ? error.message : "상품 추가에 실패했습니다")
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleDeleteVideo = async (videoId: string) => {
    if (!confirm("이 영상을 삭제하시겠습니까?")) return

    try {
      const { error } = await supabase.from("videos").delete().eq("id", videoId)

      if (error) throw error
      setVideos(videos.filter((v) => v.id !== videoId))
    } catch (error) {
      setError(error instanceof Error ? error.message : "삭제에 실패했습니다")
    }
  }

  const handleConfirmPayment = async (purchaseId: string) => {
    if (!confirm("이 구매를 승인하시겠습니까?")) return

    try {
      const { error } = await supabase
        .from("purchases")
        .update({ status: "completed" })
        .eq("id", purchaseId)

      if (error) throw error

      setPurchases(purchases.map(p => p.id === purchaseId ? { ...p, status: "completed" } : p))
    } catch (error) {
      setError("승인 처리에 실패했습니다")
    }
  }

  const getPaymentMethodLabel = (method: string) => {
    if (method.startsWith("bank_transfer")) {
      const parts = method.split(":")
      if (parts.length > 2) {
        return `무통장입금 (${parts[1]} / ${parts[2]})`
      }
      return "무통장입금 (정보 없음)"
    }
    switch (method) {
      case "bank_transfer":
        return "무통장입금"
      case "bitcoin":
        return "비트코인"
      case "litecoin":
        return "라이트코인"
      default:
        return method
    }
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "completed":
        return <span className="px-2 py-1 rounded-full bg-green-500/20 text-green-700 text-sm">완료</span>
      case "pending":
        return <span className="px-2 py-1 rounded-full bg-yellow-500/20 text-yellow-700 text-sm">대기 중</span>
      default:
        return <span className="px-2 py-1 rounded-full bg-gray-500/20 text-gray-700 text-sm">{status}</span>
    }
  }

  if (isLoading) return <div className="text-center py-12">로딩 중...</div>

  return (
    <div className="min-h-screen bg-background p-4 md:p-8">
      <div className="max-w-6xl mx-auto">
        <div className="flex items-center gap-4 mb-8">
          <Link href="/dashboard">
            <Button variant="outline" size="icon">
              <ArrowLeft size={18} />
            </Button>
          </Link>
          <h1 className="text-3xl font-bold">어드민 대시보드</h1>
        </div>

        {error && (
          <Card className="mb-6 border-red-500/50 bg-red-500/5">
            <CardContent className="pt-6">
              <p className="text-red-600">{error}</p>
            </CardContent>
          </Card>
        )}

        <Tabs defaultValue="videos" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="videos">콘텐츠 관리</TabsTrigger>
            <TabsTrigger value="purchases">구매 내역</TabsTrigger>
          </TabsList>

          <TabsContent value="videos" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>새로운 콘텐츠 추가</CardTitle>
                <CardDescription>
                  판매할 발로란트 영상 정보를 추가하세요. 영상 파일은 구매 후 직접 메일로 보내주세요.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleAddVideo} className="space-y-4">
                  <div className="space-y-4 rounded-md border p-4 bg-muted/20">
                    <Label htmlFor="thumbnail">썸네일 이미지</Label>
                    <div className="flex gap-4 items-end">
                      <div className="flex-1">
                        <Input
                          id="thumbnail"
                          type="file"
                          accept="image/*"
                          onChange={handleImageUpload}
                          disabled={isUploading}
                        />
                      </div>
                      {isUploading && <span className="text-sm text-muted-foreground pb-2">업로드 중...</span>}
                    </div>
                    {thumbnailUrl && (
                      <div className="mt-2 relative w-full h-40 bg-secondary rounded-md overflow-hidden">
                        <img src={thumbnailUrl} alt="Preview" className="w-full h-full object-cover" />
                      </div>
                    )}
                  </div>
                  <div className="space-y-4 rounded-md border p-4 bg-muted/20">
                    <Label>가격 설정 (단위: 원) - 기간별 가격을 직접 입력하세요.</Label>
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                      {["1일", "3일", "7일", "10일", "15일", "30일", "영구제"].map((duration) => (
                        <div key={duration} className="space-y-2">
                          <Label htmlFor={`price-${duration}`} className="text-xs text-muted-foreground">{duration}</Label>
                          <Input
                            id={`price-${duration}`}
                            type="number"
                            placeholder="0"
                            value={pricingTiers[duration] || ""}
                            onChange={(e) => handlePriceChange(duration, e.target.value)}
                            className="text-right"
                          />
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="space-y-4 rounded-md border p-4 bg-muted/20">
                    <Label htmlFor="product-file">제품 파일 (구매 시 다운로드될 파일)</Label>
                    <div className="flex gap-4 items-end">
                      <div className="flex-1">
                        <Input
                          id="product-file"
                          type="file"
                          onChange={handleProductFileUpload}
                          disabled={isFileUploading}
                        />
                      </div>
                      {isFileUploading && <span className="text-sm text-muted-foreground pb-2">업로드 중...</span>}
                    </div>
                    {productFileUrl && (
                      <p className="text-sm text-green-600 mt-2 break-all">
                        파일 업로드 완료: {productFileUrl}
                      </p>
                    )}
                  </div>

                  <Button type="submit" disabled={isSubmitting || isUploading || isFileUploading} className="w-full">
                    {isSubmitting ? "추가 중..." : "콘텐츠 추가"}
                  </Button>
                </form>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>나의 영상 ({videos.length})</CardTitle>
              </CardHeader>
              <CardContent>
                {videos.length === 0 ? (
                  <p className="text-muted-foreground">등록된 영상이 없습니다</p>
                ) : (
                  <div className="space-y-4">
                    {videos.map((video) => (
                      <div key={video.id} className="flex justify-between items-start p-4 border rounded-lg">
                        <div className="flex-1">
                          <h3 className="font-semibold">{video.title}</h3>
                          <p className="text-sm text-muted-foreground mt-1">{video.description}</p>
                          <div className="flex gap-4 mt-2 text-sm">
                            <span className="text-primary font-semibold">${video.price}</span>
                            <span className="text-muted-foreground">
                              {new Date(video.created_at).toLocaleDateString("ko-KR")}
                            </span>
                          </div>
                        </div>
                        <Button
                          variant="destructive"
                          size="sm"
                          onClick={() => handleDeleteVideo(video.id)}
                          className="ml-4"
                        >
                          <Trash2 size={16} />
                        </Button>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="purchases" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>구매 내역</CardTitle>
                <CardDescription>고객의 구매 정보 및 결제 현황을 확인하세요</CardDescription>
              </CardHeader>
              <CardContent>
                {purchases.length === 0 ? (
                  <p className="text-muted-foreground">구매 내역이 없습니다</p>
                ) : (
                  <div className="overflow-x-auto">
                    <table className="w-full text-sm">
                      <thead>
                        <tr className="border-b">
                          <th className="text-left py-3 px-4">고객 이메일</th>
                          <th className="text-left py-3 px-4">상품명</th>
                          <th className="text-left py-3 px-4">결제 방법</th>
                          <th className="text-left py-3 px-4">금액</th>
                          <th className="text-left py-3 px-4">상태</th>
                          <th className="text-left py-3 px-4">구매 일시</th>
                          <th className="text-left py-3 px-4">관리</th>
                        </tr>
                      </thead>
                      <tbody>
                        {purchases.map((purchase) => (
                          <tr key={purchase.id} className="border-b hover:bg-secondary/50">
                            <td className="py-3 px-4 font-mono text-xs">{purchase.user_email}</td>
                            <td className="py-3 px-4">{purchase.video_title}</td>
                            <td className="py-3 px-4">{getPaymentMethodLabel(purchase.payment_method)}</td>
                            <td className="py-3 px-4 font-semibold">₩{purchase.amount.toLocaleString()}</td>
                            <td className="py-3 px-4">{getStatusBadge(purchase.status)}</td>
                            <td className="py-3 px-4 text-muted-foreground">
                              {new Date(purchase.created_at).toLocaleDateString("ko-KR")}
                            </td>
                            <td className="py-3 px-4">
                              {purchase.status === "pending" && (
                                <Button size="sm" onClick={() => handleConfirmPayment(purchase.id)}>
                                  승인
                                </Button>
                              )}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div >
  )
}
